import { Component, OnInit, Input } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';

@Component({
  selector: 'svg-card',
  templateUrl: './svg-cards.component.html',
  styles: [],
      animations: [
        trigger('flipState', [
          state('active', style({
            transform: 'rotateY(180deg)'
          })),
          state('inactive', style({
            transform: 'rotateY(0)'
          })),
          transition('active => inactive', animate('300ms ease-out')),
          transition('inactive => active', animate('300ms ease-out'))
        ])  
      ]
})
export class CardComponent implements OnInit {

  @Input() card: string = 'spade_1';
  @Input() fill: string;
  @Input() scale: number = 1;
  private back: string = 'alternate-back';
  private temp: string;
  private color: string;
  private width: number;
  private height: number;
  private state: string = 'inactive';

  constructor() { }

  ngOnInit() {
    this.height = this.scale * 244.640;
    this.width = this.scale * 169.075;
    this.temp = this.card;
  }

  toggleState() {
    this.state = this.state === 'active'? 'inactive':'active';
    if (this.card == this.back) {
      this.card = this.temp;
      this.color = '';
    } else {
      this.card = this.back;
      this.color = this.fill;
    }
  }
}
